export default {
	data() {
		return {
			Router: this.$Router,
			swiperData: {},
			mainData: [],
			cuponList: [{}, {}],
			freeList: [{}, {}],
			current: 1,
			getBefore:{
				caseData: {
					tableName: 'Label',
					searchItem: {
						title: ['=', ['积分兑换']],
					},
					middleKey: 'category_id',
					key: 'id',
					condition: 'in',
				},
			},
			is_show: false,
			is_blackAlert: false,
		}
	},

	onLoad() {
		const self = this;
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
		self.$Utils.loadAll(['getSliderData','getMainData','getUserInfoData'], self);
	},

	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},

	methods: {
		
		getUserInfoData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				user_no:uni.getStorageSync('user_info').user_no
			};		
			postData.tokenFuncName = 'getProjectToken';
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.userInfoData=res.info.data[0];	
				} else {
					self.$Utils.showToast(res.msg,'none');
				};
				self.$Utils.finishFunc('getUserInfoData');
			};
			self.$apis.userInfoGet(postData, callback);
		},	
		
		couponAdd(index) {
			const self = this;
			var now =  (new Date()).getTime();
			const postData = {
				tokenFuncName: 'getProjectToken',
				coupon_id: self.mainData[index].id,
				data:{
					invalid_time:now + self.mainData[index].valid_time 
				}
			};
			if(self.current==1){
				postData.pay = {
					score:self.mainData[index].price
				}
			}else{
				postData.pay = {
					score: 0
				};
				//postData.data.pay_status=1
			};
			if(self.current==1){
				postData.saveFunction = [{
					FuncName: 'wxTemplate',
					searchItem:{
						user_no:uni.getStorageSync('user_info').user_no
					},
					template:'使用积分提醒',
					data_arr:{
						keyword1:{
							value:self.mainData[index].price+'分'
						},
						keyword2:{
							value:(parseFloat(self.userInfoData.score)-parseFloat(self.totalScore))+'分'
						},
					},
					thirdapp_id:2
				}];
			}
			console.log('postData', postData)
			const callback = (res) => {
				if (res && res.solely_code == 100000) {
					self.$Utils.showToast('领取成功', 'none')
				} else {
					self.$Utils.showToast(res.msg, 'none')
				}
			};
			self.$apis.couponAdd(postData, callback);
		},
		
		userCouponAdd(index) {
			const self = this;
			var now =  (new Date()).getTime();
			const postData = {
				tokenFuncName: 'getProjectToken',
			
				data:{
					invalid_time:now + self.mainData[index].valid_time,
					condition:self.mainData[index].condition,
					coupon_no:self.mainData[index].coupon_no,
					discount:self.mainData[index].discount,
					price:self.mainData[index].price,
					type:self.mainData[index].type,
					use_limit:self.mainData[index].use_limit,
					value:self.mainData[index].value,
					pay_status:1,
					status:1,
					use_step:1,
					snap_coupon:self.mainData[index]
				}
			};
		
			console.log('postData', postData)
			const callback = (res) => {
				if (res && res.solely_code == 100000) {
					self.$Utils.showToast('领取成功', 'none')
				} else {
					self.$Utils.showToast(res.msg, 'none')
				}
			};
			self.$apis.userCouponAdd(postData, callback);
		},

		change(current) {
			const self = this;
			if (current != self.current) {
				self.current = current
				if(self.current==1){
					self.getBefore = {
						caseData: {
							tableName: 'Label',
							searchItem: {
								title: ['=', ['积分兑换']],
							},
							middleKey: 'category_id',
							key: 'id',
							condition: 'in',
						},
					}
				}else{
					self.getBefore = {
						caseData: {
							tableName: 'Label',
							searchItem: {
								title: ['=', ['免费领取']],
							},
							middleKey: 'category_id',
							key: 'id',
							condition: 'in',
						},
					}
				};
				self.getMainData(true)
			}
		},
		chageSelt(selt) {
			const self = this;
			if (selt != self.selt) {
				self.selt = selt
			}
		},
		deltAlert() {
			const self = this;
			self.is_show = !self.is_show;
		},
		blackAlert() {
			const self = this;
			self.is_blackAlert=!self.is_blackAlert;
		},
		

		getMainData(isNew) {
			const self = this;
			var nowTime = Date.parse(new Date());
			if (isNew) {
				self.mainData = [];
				self.paginate = {
					count: 0,
					currentPage: 1,
					is_page: true,
					pagesize: 5
				}
			};
			const postData = {};
			postData.paginate = self.$Utils.cloneForm(self.paginate);
			postData.searchItem = {
				thirdapp_id: 2,
				start_time:['<',nowTime],
				end_time:['>',nowTime]
			};
			postData.getBefore = self.$Utils.cloneForm(self.getBefore);
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
					for (var i = 0; i < self.mainData.length; i++) {
						self.mainData[i].startTime = self.$Utils.timeto(self.mainData[i].start_time,'ymd')
						self.mainData[i].endTime = self.$Utils.timeto(self.mainData[i].end_time,'ymd')
					}
				} else {
					self.$Utils.showToast('没有更多了', 'none');
				};
				console.log('self.mainData', self.mainData)
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.couponGet(postData, callback);
		},

		getSliderData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				title: '优惠券轮播'
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.swiperData = res.info.data[0]
				}
				console.log('self.swiperData', self.swiperData)
				self.$Utils.finishFunc('getSliderData');
			};
			self.$apis.labelGet(postData, callback);
		},
	},

};
